* {
    margin: 0;
    padding: 0;
}

body {
    background-color: black;
    color: whitesmoke;

}

.header-main {
    width: 100vw;
    text-align: center;
    margin-bottom: 2rem;

}

.main-img {
    width: 80%;
}


.header-main h1 {
    display: inline-block;
    padding-top: 2rem;
    padding-bottom: 2rem;
    border-bottom: 0.2rem solid rgb(255, 214, 9);

}

.content {
    padding: 1rem;
    height: fit-content;
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    justify-items: center;
    align-items: center;
}

.content h3 {
    font-size: 1.8rem;
    text-align: left;
    font-weight: 100;
    padding-left: 1rem;
    padding-right: 1rem;
    border-left: 0.2rem solid rgb(255, 214, 9);
}




.main-wrapper {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-template-rows: repeat(2, 1fr);
    justify-items: center;
    align-content: center;
    margin-top: 2rem;
    margin-bottom: 2rem;
    grid-row-gap: 1rem;
}

@media screen and (max-width: 820px) {
    .content {
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
    }

    .content h3 {
        padding-top: 2rem;
        padding-bottom: 2rem;
        text-align: center;
        border-left: none;
        border-bottom: 0.2rem solid rgb(255, 214, 9);
    }

    .main-wrapper {
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
    }

    .main-img {
        width: 60%;
    }

}